import React, { Component } from 'react';
import styled from "styled-components";
import logo from "../assets/ic_launcher.png";
import Utils from './Utils';

class Home extends Component {

  timerInterval = 0;
  userModel = null;
  userState = null;
  workerProcess = null;

  constructor(props) {
    super(props);
    this.state = {
      userState : this.props.Model.state
    }
    this.userModel = this.props.Model;
    console.log('Home - ' + Object.getOwnPropertyNames(this.props.Model));
    this.userState = this.props.Model.state;
    console.log('User state - ' + this.userState);
  }

  async onWorkerMessage(ev) {
    this.setBatteryState({workerProcess: { instance: this.workerProcess, started: false }})
    this.callNotification();
    console.log(Utils.getBatteryTimeText(Date.now()) + ' : ' + ev.data);
  }

  setBatteryState = async (batteryOptions) => {
    this.setState({
      userState: this.userModel.setUserState(
        this.userState = {
          ...this.userState,
          ...batteryOptions,
        }
      )
    },);
  }

  getBatteryAPI = async () => {
    if ('getBattery' in navigator) {
      console.log(Utils.getBatteryTimeText(this.userState.currBatteryTime) + ' - The Battery Status API is supported on this platform.');
      console.log(this.userState);
      await navigator.getBattery().then(battery => this.setBatteryState({ batteryLevel: battery.level, batteryState: battery.charging, currBatteryTime: Date.now(), }));
    } 
    else {
      console.log('The Battery API is not supported on this platform');
    }
  }

  registerNotificationwithAccess = async () => {
    if (window.Notification && Notification.permission !== "granted") {
      Notification.requestPermission(function (status) {
        if (Notification.permission !== status) {
          Notification.permission = status;
        }
      });
    }
  }

  async callNotification() {
    //alert('Notification called @ '+ Utils.getBatteryTimeText(this.userState.currBatteryTime));
    console.log('Notification called @ '+ Utils.getBatteryTimeText(this.userState.currBatteryTime));
    const notificationBody = Utils.getBatteryStateText(this.userState.notifyBatteryState) + ' with ' + this.userState.notifyBatteryLevel.toString() + '% is now complete.';
    await Utils.showNotification(this.props.AppTitle, logo, notificationBody);
    console.log('Notification posted @ '+ Utils.getBatteryTimeText(this.userState.currBatteryTime));
    //alert('Notification posted @ '+ Utils.getBatteryTimeText(this.userState.currBatteryTime));
  }
 
  async componentDidMount() {
    this.timerInterval = setInterval(async () => { 
      await this.getBatteryAPI() 
      if(this.userState.notificationSent === false && this.userState.batteryState === this.userState.notifyBatteryState && this.userState.batteryLevel*100 === this.userState.notifyBatteryLevel) {
        this.callNotification();
        this.setBatteryState({ notificationSent : true })
        clearInterval(this.timerInterval);
      }
      else if(this.userState.batteryState === this.userState.notifyBatteryState 
        && ((this.userState.batteryState && this.userState.batteryLevel*100 >= this.userState.notifyBatteryLevel) 
            || (!this.userState.batteryState && this.userState.batteryLevel*100 <= this.userState.notifyBatteryLevel))) {
        clearInterval(this.timerInterval);
      }
    }, 1000);

    this.registerNotificationwithAccess();
    //setTimeout(() => this.callNotification(), 5000);
    
    // if(this.userState.workerProcess !== undefined && this.workerProcess === null) {
    //   this.workerProcess = this.userState.workerProcess.instance;
    //   console.log('did mount process status:', this.userState.workerProcess.started);
    //   if(!this.userState.workerProcess.started) {
    //     let obj = {'cmd': 'start', 'msg': 'from Home component'};
    //     this.workerProcess.postMessage(obj);
    //     this.setBatteryState({ workerProcess: { instance: this.workerProcess, started: true }});
    //     console.log('background worker process started');
    //   }
    //   this.workerProcess.addEventListener('message', (e) => this.onWorkerMessage(e));
    //   console.log('background worker onmessage attached');
    // }
  }

  componentWillUnmount() {
    clearInterval(this.timerInterval);
    //this.workerProcess.postMessage({'cmd': 'stop', 'msg': 'from main thread'});
    this.setBatteryState({ workerProcess: { instance: this.workerProcess, started: true }});
    console.log('did unmount process status:', this.userState.workerProcess.started);
    this.workerProcess = null;
    console.log('background worker process is still working although home component is unmounted');
}

  render() {     
    const batteryLevel = this.userState.batteryLevel*100;
    return (
      <Wrapper>
        <div className="App App-body">
            <div className="circle" 
                style={{ backgroundColor: Utils.bgColor(batteryLevel)
                        , color: Utils.fontColor(batteryLevel) }} >
              <div className="innerCircleContents" >
                <div className="contentHeader" ref="contentHeader">
                  {Utils.getBatteryStateText(this.userState.batteryState)}
                </div>
                <div className="contentBody" ref="contentBody">
                  {Utils.getBatteryLevelText(this.userState.batteryLevel)}
                </div>
                <div className="contentFooter" ref="contentFooter">
                  {Utils.getBatteryTimeText(this.userState.currBatteryTime)}
                </div>
              </div>
            </div>
            {/* <p>
              Edit <code>src/App.js</code> and save to reload.
            </p>
            <a
              className="App-link"
              href="https://reactjs.org"
              target="_blank"
              rel="noopener noreferrer"
            >
              Learn React
            </a> */}
        </div>
      </Wrapper>
    );
  }
}

const Wrapper = styled.div`
.App {
  text-align: center;
}

.App-body {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.circle {
  width:45vmax;
  height:45vmax;
  border-radius:50%;
  box-sizing: border-box;
  position:relative;
  background:rgb(240, 16, 102);
  color: black;
  font-family: Helvetica, Arial Black, sans;
  border:solid 0px greenyellow;
  transform: scale(1);
  transition:transform ease-in-out 250;
}

.circle:hover {
  transform:scale(1.010);
}

.innerCircleContents {
  margin:auto;
  padding:25% 0;
  width:100%;
  height:50%;
  place-items:center;
  display: grid;
  grid-auto-flow:row;
  grid-gap: 5px;
}

.contentHeader {
  width:50%;
  border:0px solid greenyellow;
  font-size: calc(8px + 4vmin);
}

.contentBody {
  width:100%;
  border:0px solid greenyellow;
  font-size: calc(8px + 20vmin);
}

.contentFooter {
  border:0px solid greenyellow;
  width:50%;
  font-size: calc(8px + 2vmin);
  word-wrap: break-word;
  overflow-wrap: break-word;
}
`
export default Home;
